.header {
  position: fixed;
  width: inherit;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 5;
}

.header-wrapper {
  padding: 15px 2px;
  display: flex;
  justify-content: space-between;

  @media (min-width: 768px) {
    padding: 20px 0;
  }

  @media (min-width: 1024px) {
    padding: 30px 0;
  }
}

.header-button__wrapper {
  display: none;

  @media (min-width: 1024px) {
    display: inline-block;
    vertical-align: middle;
  }
}

.header-button {
  padding: 10px 17px;
  margin-right: 60px;
  margin-top: 6px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  background: inherit;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  cursor: pointer;
}

.header-logo {
  display: flex;

  & svg {
    width: 35px;
    height: 35px;

    @media (min-width: 768px) {
      width: 55px;
      height: 55px;
    }
  }
}

.header-logo__description {
  margin-left: 15px;
  margin-top: 6px;
  display: none;

  @media (min-width: 768px) {
    display: block;
  }
}

.header-logo__description-title {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #f2f5f7;
}

.header-logo__description-subtitle {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.7);
}


.header-contacts__list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-right: 3px;
  margin-top: 2px;

  @media (min-width: 768px) {
    width: auto;
    padding-top: 7px;
    margin-right: 13px;
  }

  @media (min-width: 1024px) {
    width: 41%;
    padding-top: 10px;
    margin-right: 0;
  }

  @media (min-width: 1090px) {
    width: 38.5%;
    padding-top: 10px;
    margin-right: 0;
  }
}

.header-contacts__item a {
  position: relative;
  text-decoration: none;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
  padding-left: 37px;
}

.header-contacts__link-mail span,
.header-contacts__link-phone span {
  display: none;

  @media (min-width: 1024px) {
    display: inline-block;
    vertical-align: middle;
  }
}

.header-contacts__item:first-child {
  margin-right: 22px;

  @media (min-width: 768px) {
    margin-right: 24px;
  }

  @media (min-width: 1024px) {
    padding-left: 8px;
  }
}

.header-contacts__link-mail::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/email-icon.svg");
  width: 21px;
  height: 20px;
}

.header-contacts__link-phone::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/phone-icon.svg");
  width: 21px;
  height: 20px;
}
